<template>
    <layout>
        <div class="center">
            <cube-scroll>
            <div class="user">
                <div class="avatar">
                    <img src="https://gd3.alicdn.com/imgextra/i3/2239643951/O1CN01ywTFBz1f3bNyfkZE9_!!2239643951.jpg_400x400.jpg" alt="">
                </div>
                <div class="info">
                    <p class="name">郑明明</p>
                    <!-- <p class="desc">个人信息 > </p> -->
                </div>
            </div>
            <ul class="list">
                <li class="item">
                    <div class="logo"><i class="ye"></i></div>
                    <div class="text">我的余额</div>
                    <div class="btn" @click="_go('/recharge')">
                        <div class="go">去充值</div>
                    </div>
                </li>
                <li class="item" @click="_go('/my-address')">
                    <div class="logo"><i class="dz"></i></div>
                    <div class="text">我的地址</div>
                    <div class="btn"> > </div>
                </li>
                <li class="item" @click="_go('/my-order')">
                    <div class="logo"><i class="pj"></i></div>
                    <div class="text">我的订单</div>
                    <div class="btn"> > </div>
                </li>
                <li class="item">
                    <div class="logo"><i class="jf"></i></div>
                    <div class="text">积分</div>
                    <div class="btn"><span class="jf">3个积分</span>  </div>
                </li>
                <li class="item">
                    <div class="logo"><i class="yhq"></i></div>
                    <div class="text">优惠券</div>
                    <div class="btn"><span class="num">3张</span>  </div>
                </li>
            </ul>
            <!-- <div class="cs">
                <div class="logo"><i></i></div>
                <div class="text">我的客服</div>
                <div class="btn"> > </div>
            </div> -->
            <div class="quit">退出当前账号</div>
            </cube-scroll>
        </div>
    </layout>
</template>
<script>
export default {
    data() {
        return {};
    },
    name: 'center',
    methods: {
        _go(path) {
            this.$router.push({ path: path });
        },
    },
}
</script>
<style lang="stylus" scoped>
.center {
    font-family: HiraginoSansGB-W3;
    height: calc(100vh - 98px);
    .user {
        display: flex;
        padding: 40px 50px;
        border-bottom: 0.5px solid #F2F2F2;
        background-color: #ffffff;
        .avatar {
            width:120px;
            height:120px;
            border-radius:50%;
            img {
                width:120px;
                height:120px;
                border-radius:50%;
            }
        }
        .info {
            padding-top: 22px;
            padding-left: 30px;
            .name {
                font-size: 36px;
                font-weight: bold;
                color: #333333;
            }
            .desc {
                margin-top: 18px;
                font-size: 24px;
                color: #999999;
            }
        }
    }
    .list {
        padding: 0 30px;
        background-color: #ffffff;
        .item {
            height: 98px;
            border-bottom: 0.5px solid #F2F2F2;
            display: flex;
            align-items: center;
            justify-content: space-between;
            position: relative;
            .logo {
                width: 32px;
                i {
					display: inline-block;
					width: 32px;
					height: 32px;
					background-size: 100% 100%;
					background-repeat: no-repeat;
                }
                .ye {
					background-image: url(../../../static/image/center/icon_wdye@2x.png);                
                }
                .dz {
					background-image: url(../../../static/image/center/icon_wddz@2x.png);                
                }
                .pj {
					background-image: url(../../../static/image/center/ic_user_appraisal@2x.png);                
                }
                .jf {
					background-image: url(../../../static/image/center/icon_jf@2x.png);                
                }
                .yhq {
					background-image: url(../../../static/image/center/icon_yhq@2x.png);                
                }
            }
            .text {
                flex: auto;
                margin-left: 32px;
                font-size: 28px;
                color: #333333;    
            }
            .btn {
                min-width: 120px;
                text-align: right;
                color: #CCCCCC;
                .jf {
                    font-size: 28px;
                    color: #FF8C45;
                }
                .num {
                    font-size: 28px;
                    color: #FF8C45;   
                }
                .go {
                    width: 120px;
                    height: 48px;
                    background: #FF8C45;
                    border-radius: 4px;
                    font-size: 20px;
                    color: #ffffff;
                    line-height: 48px;
                    text-align: center;
                }
            }
        }
    }
    .cs {
        padding: 0 30px;
        margin-top: 21px;
        background-color: #ffffff;
        height: 98px;
        display: flex;
        align-items: center;
        .logo {
            width: 32px;
            i {
				display: inline-block;
				width: 32px;
				height: 32px;
				background-image: url(../../../static/image/center/ic_user_consult@2x.png);
				background-size: 100% 100%;
				background-repeat: no-repeat;
            }    
        }
        .text {
            flex: auto;
            margin-left: 32px;
            font-size: 28px;
            line-height: 32px;
            color: #333333;    
        }
        .btn {
            min-width: 120px;
            color: #CCCCCC;
            text-align: right;
        }
    }
    .quit {
        margin-top: 20px;
        height: 98px;
        line-height: 98px;
        text-align: center;
        background: #ffffff;
        font-size: 28px;
        color: #F43030;
    }
}
</style>



